@import url('https://fonts.googleapis.com/css?family=Quicksand:400,500,700');

body {
      background: white;
      font-family: 'Quicksand', sans-serif;

}
.App {
    width: 900px;
    margin: 0 auto;
    display: block;
    max-width: 100%;
    border: 1px solid #f0f0f0;
    margin-top: 43px;
    padding-bottom: 20px;
}
img.App-logo {
    width: 100%;
}
h2 {
    background: #bc1a41;
    color: white;
    text-align: right;
    margin-top: -4px;
    padding: 9px 20px;
    text-transform: uppercase;
    font-size: 15px;
}
.group {
    border-radius: 18px;
    display:none;
}
.Group1 {
  display:block;
}
.Question {
    padding: 13px 38px 13px 38px;
    color: #bc1a41;
}
h3 {
    color: #333;
    padding: 00px;
    margin: 0px;
    margin-bottom: 8px;
}
.button, button {
    margin-top: 15px;
    display: block;
    margin-bottom: 0px;
    text-align: center;
    background: #bc1a41;
    color: white;
    text-transform: uppercase;
    border: none;
    font-size: 15px;
    font-weight: bold;
    padding: 10px 20px;
    width: 100%;
    cursor: pointer;
    width: 217px;
    margin-left: 39px;
}

.button:hover,button:hover {
    background: #921030;
}
label {
    margin-right: 15px;
}
.results {
    text-align: center;
}
li.listitem {
    margin: 0 auto;
    display: block;
    list-style: none;
    margin-bottom: 5px;
    font-size: 23px;
}
li.listitem a {
    width: 300px;
    display: block;
    margin: 0 auto;
    background: #f0f0f0;
    text-decoration: none;
    padding: 8px 0px;
    color: #333;
}
li.listitem a:hover {
    background: #bc1a41;
    color: white;
}
.results h3 {
    width: 92%;
    margin: 0 auto;
    margin-bottom: 30px;
    margin-top: 0px;
}
.results h1 {
    width: 92%;
    margin: 0 auto;
    margin-bottom: 10px;
    font-size: 29px;
    padding-top: 26px;
}
.resultbox {
	display:none;
}
.SubscribeForm {
	display:block;
}
.formlabel {
  margin-bottom: 4px;
font-weight: bold;
font-size: 16px;
}
.formbox {
    padding: 32px;
    text-align: left;
}
.formbox .button {
    margin-left: 0px;
}
.formbox input#fname,.formbox input#emailAddress {
    width: 253px;
    height: 15px;
    padding: 10px;
    font-size: 12px;
}
@media only screen
and (max-width : 689px) {
/* Styles */
label {
    margin-right: 15px;
    display: block;
    margin-bottom: 6px;
}
}
.item1 a {
    background: #C71585!important;
    color: white!important;
}
.item2 a {
    background: #DB7093!important;
    color: white!important;
}
.item3 a {
    background: #FF1493!important;
    color: white!important;
}
.item4 a {
    background: #FF69B4!important;
    color: white!important;
}
.results h3 {    margin-top: 0px;
    padding-top: 27px;
}


